.flip-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #000;
  background-color: #ffffff;
  width: 30px;
  height: 30px;

  border: none;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 8px rgb(224, 224, 224);

  transition: all 0.5s;
}

.flip-button--back {
  left: 0;
}

.flip-button--next {
  right: 0;
}

.flip-button:hover {
  background-color: #f3b60e;
  box-shadow: 0 0 8px rgb(235, 113, 13);
}
