.Carousel {
  position: relative;
  padding: 0 40px;
}

.Carousel__list {
  display: flex;
  overflow: hidden;
  list-style: none;
  margin: 0;
  padding: 0;
}

.Carousel__list li {
  position: relative;
  right: 0;
  transition: right 1s;
}

@keyframes appearance {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
